<template>
  <mt-tabbar v-model="selected" id="footer">
    <mt-tab-item id="" >
      <i slot="icon"  class="iconfont icon-zhuye"/>
      
    </mt-tab-item>
    <mt-tab-item id="">
       <i slot="icon"  class="iconfont icon-shangcheng"/>
      
    </mt-tab-item>
    <mt-tab-item id="">
       <i slot="icon"  class="iconfont icon-qiche1"/>
      
    </mt-tab-item>
    <mt-tab-item id="">
      <i slot="icon"  class="iconfont icon-Icon_wode" />
        {{getUser.username?'':''}}
    </mt-tab-item>
  </mt-tabbar>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "TabBar",
  data() {
    return {
      selected:"",
    };
  },
  computed: {
    ...mapGetters(["getUser"])
  },
  watch:{
    selected:function(){
      switch(this.selected){
        case "":this.$router.push('/home/all?type=__all__');break;
        case "":this.$router.push('/my');break;
        case "":this.$router.push('/car');break;
        case "":this.$router.push('/hypermarket');break;
      }
    },
    
  }
};
</script>

<style lang="less" scoped>
#footer {
  z-index: 996;
  width: 100%;
  height: 1.4rem;
  position: fixed;
  left: 0;
  bottom: 0;
  border-top: 1px solid #ccc;
  background: #fff;
}


#footer .iconfont {
  font-size: 0.5rem;
  padding-bottom: 0.1rem;
}

</style>
