<template>
  <div class="table1">
    <router-link
      v-for="(item,index) in name"
      :key="index"
      :to="item.path"
      tag="div"
      @click.native="handSpan(index)"
    >
      {{item.name}}
      <span v-show="spanIndex==index"></span>
    </router-link>
  </div>
</template>
<script>
export default {
  name: "table1",
  data() {
    return {
      name: [
        {
          name: "",
          path: "/car"
        },
        {
          name: "",
          path: "/car/CarSelection"
        },
        {
          name: "",
          path: "/car/Myring"
        },
        // {
        //   name: "",
        //   path: "/car/Smallvideo"
        // },
        {
          name: "",
          path: "/car/Rankinglist"
        },
        {
          name: "",
          path: "/car/Addedservice"
        },
        {
          name: "",
          path: "/car/Essay"
        }
        // {
        //   name: "",
        //   path: "/car/Video"
        // }
      ],
      spanIndex: 0
    };
  },
  methods: {
    handSpan(index) {
      this.spanIndex = index;
      // this.$observer.$emit("add", index);
      // console.log(this.spanIndex);
    }
  },
  created() {
    this.$observer.$on("tableIndex", data => {
      this.spanIndex = data;
    });
    this.$observer.$on("span", msg => {
      this.spanIndex = msg;
    });
    this.$observer.$on("tableList", msgTable => {
      this.spanIndex = msgTable.index;
    });
    if (this.spanIndex == 0) {
      this.$router.push("/car");
    }
  }
};
</script>
<style lang="scss" scoped>
#app {
  width: 100%;
  height: 100%;
}
.table1 {
  position: fixed;
  left: 0;
  top: 1.4rem;
  height: 1.4rem;
  width: 100%;
  background: #333333;
  display: flex;
  overflow-x: auto;

  div {
    width: 17%;
    height: 100%;
    flex: 0 0 auto;
    text-align: center;
    align-self: center;
    font-size: 0.45rem;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-sizing: border-box;
    span {
      position: absolute;
      top: 1.1rem;
      display: block;
      width: 0.9rem;
      height: 0.1rem;
      align-self: center;
      background: #ffe100;
    }
  }
}
</style>
